import React from 'react'
import { CSSTransition } from 'react-transition-group'
import './info.css'

const info = 
`2018/11/13
- 
- 
- 
- 
- Edgebug (IE)

2018/10/18
- 

2018/10/16
- Overdrive

2018/10/14
- HPBug
- 
- wiki
- 

2019/10/8
- `


function Info(props) {
  return (
    <CSSTransition classNames='info' in={props.info.active} timeout={250}>
      <div className='info' >
        <div className='info-div'>
          <pre className='info-content'>{info}</pre>
        </div>
      </div>
    </CSSTransition>
  )
}

export default Info